<template>
  <v-container
    id="regular-tables"
    fluid
    tag="section"
  >
    <base-material-card
      icon="mdi-text-box"
      title="Novo Orçamento"
      class="px-5 py-3"
    >
      <base-back-button />
      <base-loader :show="loading" />
      <v-row align="center">
        <carrier-form
          :carrier="carrier"
          :save="save"
        />
      </v-row>
      <base-material-snackbar
        type="primary"
        icon="mdi-firework"
        dark
        dismissible
        v-bind="{
          ['top']: true,
          ['left']: true
        }"
      >
        Registro de operadora incluído com sucesso!
      </base-material-snackbar>
    </base-material-card>
  </v-container>
</template>
<script>

  export default {
    name: 'CarrierCreate',
    components: {
      CarrierForm: () => import('./form'),
    },
    data: function () {
      return {
        sucess: false,
        loading: false,
        carrier: {
          id: undefined,
          code: undefined,
          name: undefined,
          shortName: undefined,
        },
      }
    },
    methods: {
      save (carrier) {
        this.loading = true
        this.$http.post('api/carrier', carrier, (response) => {
          this.loading = false
          if (response.status === 201) {
            this.$toasted.show('Salvo com sucesso', {
              position: 'top-right',
              type: 'success',
              duration: 2000,
            })
            this.$router.push({ name: 'Cadastro de Operadoras' })
          }
        }, (error) => {
          this.writeLog(error)
          this.loginError = true
        })
      },
    },
  }
</script>
